<template>
  <div class="navbar" style="margin:-10px;">
     <el-card shadow="hover" id="head">
      <br>
      <i :class= "{'el-icon-sunny':check1,'el-icon-moon':check2}" style="font-size: 30px; display: inline; margin:30px;"></i>
      <h3 id="mes" style="display: inline; font-size:30px"></h3>
      </el-card>  
  </div>
</template>

<script>
export default {
  name: 'navbar',
  data(){
    return{
           check1:true,
           check2:false,
           time: new Date(),
           mesnight:                                                                                                                     ["久坐伤身，身下懒腰吧。","忙碌了一天，放松下吧。","今天的努力就是明天的底气。","任何值得而拥有的都是值得等待的。","拂尘晚归，得空闲坐。","今天离小目标又近了一步。","百草处黄消热暑，篱边瓜果缀枝头。","前路浩浩荡荡，万物皆可期待。","勤者读书夜达旦，青藤绕屋花连云。"],
           mesmor:                                                                                                                    ["人生最美的风景在前方","你认真的模样真好看","心有多大，世界就有多大","既然选择了远方，便只顾风雨兼程。","元气满满的一天开始啦。","又是元气满满的一天。","今天也要加油哦。","常应常静常清净矣。","听首音乐，读本好书。","小池残暑退，高树早凉归。","离离暑云散，习习凉风起。"],
           output:"",
          activities: [{
          content: 'QA测试版上线',
          timestamp: '2020-08-27'
        }, {
          content: 'QA正式版上线',
          timestamp: '2020-09-01'
        }, {
          content: 'QA正式2.0版上线',
          timestamp: '2020-09-12',
          color: '#0bbd87'
        }]
    }
  },
  mounted(){
    if(this.time.getHours()>=5 && this.time.getHours()<11){
      let index = Math.floor(Math.random()*this.mesmor.length);
       this.output = "早上好，" + this.mesmor[index]; 
    }
    else if(this.time.getHours()>=11 && this.time.getHours()<13){
      let index = Math.floor(Math.random()*this.mesmor.length);
       this.output = "中午好，" + this.mesmor[index];
    }
    else if(this.time.getHours()>=13 && this.time.getHours()<17){
      let index = Math.floor(Math.random()*this.mesnight.length);
      this.output = "下午好，" + this.mesnight[index];
    }
    else if(this.time.getHours()>=17 && this.time.getHours()<22){
      let index = Math.floor(Math.random()*this.mesnight.length);
      this.output = "晚上好，" + this.mesnight[index];
      this.check1 = false;
      this.check2 = true;
    }
    else{
      this.output = "很晚了，要好好休息哦";
      this.check1 = false;
      this.check2 = true;
    }
    document.getElementById('mes').innerHTML=this.output;
  }
}
</script>

